.list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.item {
  width: 200px;
  height: 250px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  list-style: none;

  border: 3px solid cornflowerblue;
  border-radius: 12px;
}
